.CreateTodoButton {
  background-color: #212121;
  box-shadow: 2px 5px 25px rgba(97, 250, 199, 0.5);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 40px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;

  transform: rotate(0);
  transition: 0.3s ease;
  z-index: 1;
}

.CreateTodoButton:hover {
  transform: rotate(224deg);
}
